/* new style for card */

.Conatiner-submit{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  
}

.cart-card{
  flex-grow:4;
  display: flex;
  flex-direction: row;
  width: 60%;
  justify-content: space-evenly;
  flex-wrap: wrap;
 }




.cart-card .cart{
  border: 0.5px solid lightgrey;
  /* text-align: center; */
  margin-left: 1%;
  margin-right: 1%;
  padding-left: 10px;
  width: 42%;
  
  margin-bottom: 12%;
}

.cart:hover {
  box-shadow: 0 0 5px rgba(15, 15, 15, 0.26);
  transform: translateY(-1vmax);
}


.cart-card .cartImage {
  align-items: center;
   width: 100%;
}

.cart-card .cart-summary{
  margin-left: 10%;
  width: 25%;
}



@media screen and (max-width: 700px) {

  .cart-card .cart{
    width: 100%;
  }

}