.category-Sec{
  display: grid;
  justify-content: center;
  align-content: center;
  place-items: center;  
  font-size: 16px;}

  .category-section::-webkit-scrollbar{
    display: none;
  }

.category-section{
overflow-x: auto;
overscroll-behavior-x: contain;
/* border: 1px solid rgb(204, 204, 204);   */
padding: 2rem;
width: 100%;

}

.cat-btn {
display: grid;
grid-auto-flow: column;
grid-auto-columns:5rem;
grid-gap: 3rem;
/* padding-left: 1%; */
}
.cat-btn .centent{
  display: grid;
  grid-gap: 0.5rem;
  margin: 0;
  text-align: center;
  position:relative;
  user-select: none;
  transition: all 0.2 ease-in;

}

.cat-btn {
    padding: 0%;
    font-size:16px;
    text-transform: none;
}
.cat-btn .centent:hover{
  transform: scale(1.1);
}

.cat-btn .centent .card-category{
  display: inline-block;
  width: 5rem;
  height: 5rem;
  border-radius: 50%;

}

.cat-btn .centent .card-category img{
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  clip-path: circle(42%);
}

.cat-btn .centent:last-child::after{
  content: '';
  position: absolute;
  width: 2rem;
  height: 100%;
  right: -2rem;
}


.cat-btn .centent .text-container{
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 0.9rem;
}

.ctn{
  overflow-x: auto;
overscroll-behavior-x: contain;
/* width: 90%; */
}

