.productCard {
  width: 20%;
  height: 30%;

  display: flex;
  /* border: 0.5 solid lightgray; */
  border-radius: 5%;
  flex-direction: column;
  text-decoration: none;
  color: rgb(48, 48, 48);
  margin: 2vmax;
  transition: all 0.5s;
  padding-bottom: 0.5vmax;
}

.productCard > img {
  width: 100%;
}

.productCard > div {
  /* margin: 0.5vmax; */
  display: flex;
  
  justify-content: flex-start;
  align-items: center;
}

.productCard .description {
    color: black;
    font-size: 95%;
    font-family: "Roboto";
    margin:0.5vmax;
  padding: 0%;
    text-align: start;
  }

.productCardSpan {
  margin: 0%;
  font: 300 0.7vmax "Roboto";
}

.productCard > p {
  font-family: "Roboto";
  font-size: 1.2vmax;
  margin: 1vmax 0.5vmax;
  margin-bottom: 0;
}

.productCard > span {
  margin: 0.5vmax;
  color: tomato;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-size: 1vmax;
}

.productCard:hover {
  box-shadow: 0 0 5px rgba(15, 15, 15, 0.26);
  transform: translateY(-1vmax);
}



