.reviews .offcanvas.offcanvas-bottom {
  right: 15%;
  left: 15%;
  height:40%;
  max-height: 100%;
  border-top: var(--bs-offcanvas-border-width) solid var(--bs-offcanvas-border-color);
  transform: translateY(100%);
}
#review-text{
  border: 1px solid lightblue;
}

.extra-img{
  display: none;
}

.reviews  .offcanvas p{
  margin: 0%;
  padding: 0%;
}
.reviews .offcanvas {
  border-radius: 20px;
  position: fixed;
  bottom: 40%;
  z-index: var(--bs-offcanvas-zindex);
  display: flex;
  flex-direction: column;
  max-width: 70%;
  color: var(--bs-offcanvas-color);
  visibility: hidden;
  background-color: var(--bs-offcanvas-bg);
  background-clip: padding-box;
  outline: 0;
  transition: var(--bs-offcanvas-transition);
}

.product-details p{
  margin: 0%;
  padding: 1%;
}

.addCart{
  background-color: white;
  color: black;
  border: 1px solid black;
  border-radius: 10px;
}
.addCart:hover{
  background-color: black;
  color: white;
}
.product-details-info {
margin-top: 100px;
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  /* justify-content: center; */
}


.card-title{
  width: 20%;
  height: 40%;
}
.cards{
  width: 40%;
  height: 85%;
}

.card-title img{
  border-radius: 60%;
  width: 100% ;
  height: 100%;
}


