.productCard {
  width: 20%;
  height: 30%;
  display: flex;
  flex-direction: column;
  text-decoration: none;
  color: rgb(48, 48, 48);
  margin: 2vmax;
  transition: all 0.5s;
  padding-bottom: 0.5vmax;
}

.productCard > img {
  width: 100%;
  height: 100%;
}

.productCard > div {
  /* margin: 0.5vmax; */
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.productCard .description {
    color: black;
    font-size: 95%;
    font-family: "Roboto";
    margin:0.5vmax;
  padding: 0%;
    text-align: start;
  }

.productCardSpan {
  margin: 0.5vmax;
  font: 300 0.7vmax "Roboto";
}

.productCard > p {
  font-family: "Roboto";
  font-size: 1.2vmax;
  margin: 1vmax 0.5vmax;
  margin-bottom: 0;
}

.productCard > span {
  margin: 0.5vmax;
  color: tomato;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-size: 1vmax;
}

.productCard:hover {
  box-shadow: 0 0 5px rgba(15, 15, 15, 0.26);
  transform: translateY(-1vmax);
}

@media screen and (max-width: 700px) {
  
.productCard {
  width: 40%;
  height: 52%;
 }

  .productCard > p {
    font-size: 100%;
    font-weight: 700;
  }
  
.product-details-info {
margin-top: 0px;
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  justify-content: center;
}

  .productCard h3 {
    margin: 0vmax;
    color: black;
    font-size: 95%;
    /* font-weight: 0; */
  }

  .productCard > div {
    margin: 0vmax;
    /* display: block; */
  }

  .productCard > span {
    font-size: 70%;
  }

  .productCard > div > span {
    margin: 0 0.5vmax;
    
    font: 300 65% "Roboto";
  }
  .product-details {
    margin-top: 50px !important;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
   
  }
  .cards {
    height: 100%;
    width: 100%;
}
  .similar-products  {
    margin-top: 5%;
    margin-bottom: 15%;
  }

  .extra-img{
    width: 60%;
    display: block;
  }
}
