.admin li{
    list-style: none;
    padding: 3px 20%;
margin-top: 10px;
width: 100%;
    border: 1px solid lightgrey;
    /* border-bottom: 0px; */
}
.admin  li .btn-list{
width: 100%;
}
.admin .active{
    /* width: 100%; */
    text-decoration: none;
    text-decoration-line:none;
    /* background-color: purple; */
    color: red;

}
.offcanvas {
    position: fixed;
    
    bottom: 0;
    z-index: var(--bs-offcanvas-zindex);
    display: flex;
    flex-direction: column;
    max-width: 60%;
    color: var(--bs-offcanvas-color);
    visibility: hidden;
    background-color: var(--bs-offcanvas-bg);
    background-clip: padding-box;
    outline: 0;
    transition: var(--bs-offcanvas-transition);
}
.headingbtn{
    border: 1px solid black;
    border-radius: 10px;
    margin-bottom: 20px;
}
 

.hide-web{
    display: none;
    }



@media (max-width: 700px){
 .hide-mobile{
    display: none;
 }
.hide-web{
display: block;
} }